


.info {
	min-height: 400px;
	margin-bottom: 100px;
	border-top: 2px solid #f7040e;
	border-bottom: 2px solid #f7040e;
}

.info__list {
	padding-left: 0;
	width: 90%;
	margin: auto;
	list-style: none;
}

.info__item {
	display: flex;
	align-items: center;
	justify-content: space-between;
}

.info__b {

	font-family: "arial";
	font-size: 24px;
	text-align: left;
	padding-left: 50px;
	margin-right: 250px;
}

.info__text {
	width: 550px;
	font-family: "Arial";
	font-size: 20px;
}

.info__item--dark {
	background-color: #920ff0;
}