

.slider {
	min-height: 500px;
}

.slider__wrapper {
	display: flex;
	height: 450px;
	margin-bottom: 50px;
	margin-left: 50px;
	margin-right: 50px;
}


.slide2,
.slide4 {
	flex-direction: row-reverse;
}

.slider__img1 {
	width: 50%;
	height: 450px;
	background-image: url("../img/im-1.jpg");
	background-size: 100%;
	background-repeat: no-repeat;

}

