}

.slider__text {
	width: 50%;
	font-family: "Arial";
	padding-left: 15px;

	background-image: url("../img/rad.png");
	background-size: 850px 1000px;
	background-position: -100px;
}

.slider__title {
	text-align: center;
	text-transform: uppercase;
}

