.main {
  min-height: 2000px;
  background-color: white;
  background-size: 100% 100%;
  background-position: 10px 100px;
  animation: city 30s linear infinite; }

@keyframes city {
  from {
    background-position: 100% -1000px, 0 0; }
  to {
    background-position: 100% 0; } }

.video {
  background-color: transparent;
  margin: auto; }

.header {
  height: 100vh;
  background: url("../img/fnp-9.jpg"), url("../img/logo.png");
  background-size: 100% 100vh, 50px 50px;
  background-position: 0 0, top left;
  background-repeat: no-repeat; }

.header__nav {
  display: flex;
  flex-direction: column;
  height: 600px; }

.header__list {
  display: flex;
  justify-content: space-around;
  width: 100%;
  height: 45px;
  align-self: flex-end;
  list-style: none;
  padding-left: 0;
  background: linear-gradient(90deg, #f70210 0%, #a90abe 72%, #a00cd6 100%);
  margin-top: 0; }

.header__item {
  font-family: Arial;
  font-size: 16px;
  text-transform: uppercase;
  align-self: center; }

.header__link {
  color: white;
  text-decoration: none; }
  .header__link:hover {
    border-bottom: 2px solid white; }

.slider {
  min-height: 500px; }

.slider__wrapper {
  display: flex;
  height: 450px;
  margin-bottom: 50px;
  margin-left: 50px;
  margin-right: 50px; }

.slide2,
.slide4 {
  flex-direction: row-reverse; }

.slider__img1 {
  width: 50%;
  height: 450px;
  background-image: url("../img/im-1.jpg");
  background-size: 100%;
  background-repeat: no-repeat; }

.slider__img2 {
  width: 50%;
  height: 450px;
  background-image: url("../img/img-3.jpg");
  background-size: 100%;
  background-repeat: no-repeat; }

.slider__img3 {
  width: 50%;
  height: 450px;
  background-image: url("../img/img-4.jpg");
  background-size: 100%;
  background-repeat: no-repeat; }

.slider__img4 {
  width: 50%;
  height: 450px;
  background-image: url("../img/img-5.jpg");
  background-size: 100%;
  background-repeat: no-repeat; }

.slider__text {
  width: 50%;
  font-family: "Arial";
  padding-left: 15px;
  background-image: url("../img/rad.png");
  background-size: 850px 1000px;
  background-position: -100px; }

.slider__title {
  text-align: center;
  text-transform: uppercase; }

.info {
  min-height: 400px;
  margin-bottom: 100px;
  border-top: 2px solid #f7040e;
  border-bottom: 2px solid #f7040e; }

.info__list {
  padding-left: 0;
  width: 90%;
  margin: auto;
  list-style: none; }

.info__item {
  display: flex;
  align-items: center;
  justify-content: space-between; }

.info__b {
  font-family: "arial";
  font-size: 24px;
  text-align: left;
  padding-left: 50px;
  margin-right: 250px; }

.info__text {
  width: 550px;
  font-family: "Arial";
  font-size: 20px; }

.info__item--dark {
  background-color: #920ff0; }

.contacts {
  width: 37%;
  height: 200px;
  position: absolute;
  top: 50%;
  left: 5%;
  color: white;
  background-image: url("../img/logo.png");
  background-size: 250px 250px;
  background-position: 420px 50px;
  background-color: white;
  background-repeat: no-repeat; }

.contacts__title {
  font-family: "Arial";
  text-align: center;
  color: black; }

.contacts__text {
  font-family: "Arial";
  margin-top: 55px;
  color: black; }

.contacts__link {
  font-family: "arial";
  text-decoration: none;
  color: black; }
