

.header {
	height: 100vh;
	background: url("../img/fnp-9.jpg"), url("../img/logo.png");
	background-size: 100% 100vh, 50px 50px;
	background-position: 0 0, top left;
	background-repeat: no-repeat;

}

.header__nav {
	display: flex;
	flex-direction: column;
	height: 600px;

}

.header__list {
	display: flex;
	justify-content: space-around;
	width: 100%;
	height: 45px;

	align-self: flex-end;
	list-style: none;
	padding-left: 0;

	background: linear-gradient(90deg, rgba(247,2,16,1) 0%, rgba(169,10,190,1) 72%, #a00cd6 100%);
	margin-top: 0;
}

.header__item {
	font-family: Arial;
	font-size: 16px;
	text-transform: uppercase;
	align-self: center;
}

