

.contacts {
	width: 37%;
	height: 200px;
	position: absolute;
	top: 50%;
	left: 5%;
	color: white;

	background-image: url("../img/logo.png");
	background-size: 250px 250px;
	background-position: 420px 50px;
	background-color: white;
	background-repeat: no-repeat;
}

.contacts__title {
	font-family: "Arial";
	text-align: center;
	color: black;
}


.contacts__text {
	font-family: "Arial";
	margin-top: 55px;
	color: black;
}

.contacts__link {
	font-family: "arial";
	text-decoration: none;
	color: black;
}